.app {
  height: 100vh;

  .sections {
    width: 100%;
    height: calc(100vh - 70px);
    background-color: lightsalmon;
    position: relative;
    top: 70px;
    scroll-snap-type: y mandatory;
    scrollbar-width: none; // for firefox
    &::-webkit-scrollbar{
      display: none;
    } // for chrome & others
    scroll-behavior: smooth;

    > * {
      width: 100vw;
      // height: calc(100vh - 70px);
      scroll-snap-align: start;
    }
  }
}